/* App.css */

.App {
  text-align: center;
  padding: 20px;
}

.options-container {
  background-color: #f0f0f0;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 0px;
  margin: 10px 0;
  display: inline-block;
}

.button-container-left {
  text-align: left;
  padding: 20px;
}

.display-modal {
  text-align: left;
  padding: 20px;
}

button {
  background-color: #44c767;
  color: #fff;
  border: none;
  padding: 12px 24px;
  cursor: pointer;
  font-family: 'Helvetica', sans-serif;
  border-radius: 0px;
}

button:hover {
  background-color: #a4aaaf;
}

select {
  padding: 8px; /* Adjust padding for the select elements */
  font-size: 16px;
}

.kanban-board {
  display: flex;
  justify-content: space-around;
}

/* KanbanColumn.css */

.kanban-column {
  flex: 1;
  border: 1px solid #cccccc;
  border-radius: 0px;
  padding: 10px;
  margin: 10px;
  background-color: #f4f4f4;
}

h2 {
  font-size: 20px; /* Increase the font size of h2 */
  margin-bottom: 15px; /* Adjust margin */
  color: #333; /* Set a different color for h2 */
}

/* TaskCard.css */


.task-card {
  background-color: #ccc; /* Default grey background color */
  border-radius: 0px;
  padding: 15px;
  margin: 10px 0;
  transition: background-color 0.3s; /* Smooth transition effect */

  /* Other task-card styles */
}

.task-card.expanded {
  background-color: #fff; /* Change to white when expanded */
}

.task-card.collapsed {
  background-color: #ccc; /* Back to grey when collapsed */
}
.task-details {
  background-color: #f0f0f0; /* Set the background color to grey */
  padding: 10px;
  border: 1px solid #ccc; /* Grey border */
  border-radius: 0px;
  margin-top: 10px; /* Add margin to separate it from the title */
  display: inline-block; /* Set the container to display as an inline block */
}


.toggle-button {
  background-color: transparent; /* Set the background color to transparent */
  border: none;
  padding: 0;
  margin: 0;
  color: #0f100f; /* Change the text color to blue */
  cursor: pointer;
  opacity: 1; /* Adjust the opacity to make it almost transparent */
}

h3 {
  font-size: 14px; /* Increase the font size of h3 */
  margin-bottom: 6px; /* Adjust margin */
  color: #444; /* Set a different color for h3 */
}

p {
  font-size: 16px; /* Increase the font size of paragraphs */
  margin: 8px 0; /* Adjust margin */
  color: #666; /* Set a different color for paragraphs */
}

/* Add more styling as needed */
.Group,#Group{
text-align: left;
}
body {
  /* From https://css.glass */
  background: #bbb7b746; /* Set the background color to a greyish-white color */
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(7.9px);
  -webkit-backdrop-filter: blur(7.9px);
  border: 1px solid rgba(204, 242, 253, 0.53);
}



